@import "~@/assets/scss/production.scss";@import "~@/assets/scss/variables.scss";@import "~@/assets/scss/functions.scss";@import "~@/assets/scss/mixin.scss";
@include b(live) {
  overflow: hidden;
  background-color: #1e1e1e;

  @include e(content) {
    width: $--content-width;
    margin: 40px auto;
  }
}

@include b(underline) {
  position: relative;

  &::after {
    position: absolute;
    right: 0;
    bottom: -10px;
    left: 0;
    height: 2px;
    content: '\20';
    border-bottom: 2px solid #cc2944;
  }
}

@include b(banner) {
  height: 297px;
  background: #141414 no-repeat center -3px;

  @include background-image(url(~@/views/live/img/banner.jpg));

  @include e(inner) {
    width: $--content-width;
    padding-left: 620px;
    margin: 0 auto;
    overflow: hidden;
  }

  @include e(title) {
    position: relative;
    margin-top: 52px;
    font-size: 24px;
    color: #fff;
  }

  @include e(subtitle) {
    margin-top: 30px;
    font-size: 14px;
    line-height: 20px;
    color: #d33442;

    & + & {
      margin-top: 0;
      color: #b0b0b0;
    }
  }

  @include e(icon-exclusive) {
    position: relative;
    top: -2px;
    width: auto;
    height: 20px;
    margin-left: 6px;
    vertical-align: middle;
  }

  @include e(link) {
    margin-top: 10px;
    font-size: 14px;
    color: $--color-primary;

    a:hover {
      text-decoration: underline;
    }
  }

  @include e(button-wrapper) {
    margin-top: 22px;
  }

  @include e(button) {
    width: 120px;
    height: 36px;
    font-size: 16px;
    line-height: 36px;
    border-radius: $--border-radius-base;
    transition: all 0.2s ease;

    & + & {
      margin-left: 15px;
    }

    &:first-child {
      color: #fff;
      background: #ff884d linear-gradient(90deg, #ff884d, #e2304a);
    }

    &:last-child {
      line-height: 34px;
      color: $--color-primary;
      border: 1px solid $--color-primary;
    }

    &:hover {
      &:first-child {
        background: #ff884d linear-gradient(180deg, #ff884d, #e2304a);
      }

      &:last-child {
        color: rgba(204, 163, 82, 0.9);
        border: 1px solid rgba(204, 163, 82, 0.9);
      }
    }
  }
}

@include b(game) {
  float: left;
  width: 590px;
  height: 225px;
  margin-bottom: 20px;
  box-shadow: 1px 1px 4px rgba(0, 0, 0, 0.2);

  @include e(img-wrapper) {
    position: relative;
    float: left;
    width: 364px;
    height: 100%;

    &:hover {
      @include e(mask) {
        visibility: visible;
        opacity: 1;
      }

      @include e(rule-item) {
        transform: translateY(0);
      }
    }
  }

  @include e(img) {
    width: 100%;
    height: 100%;
    vertical-align: top;
  }

  @include e(icon-hot) {
    position: absolute;
    top: 10px;
    right: 18px;
    width: 63px;
    height: 30px;
    background-position: -115px -88px;

    @include background-image(url(~@/assets/img/common/common_icons_1.png));
  }

  @include e(mask) {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    visibility: hidden;
    background: linear-gradient(rgba(0, 0, 0, 0), rgba(0, 0, 0, 1));
    opacity: 0;
    transition: all 0.2s ease;
  }

  @include e(rule-list) {
    padding-top: 134px;
    margin: 0 auto;
    text-align: center;
  }

  @include e(rule-item) {
    display: inline-block;
    height: 30px;
    padding: 0 12px;
    margin: 4px;
    font-size: 14px;
    line-height: 28px;
    color: #fff;
    vertical-align: top;
    cursor: pointer;
    border-radius: 3px;
    transition: all 0.2s;
    transform: translateY(-10px);

    @for $i from 0 through 6 {
      $color: hsla(random(360), 50%, 50%, 0.8);
      &.item-#{$i} {
        border: 1px solid $color;

        &:hover {
          background-color: $color;
          border: 1px solid transparent;
        }
      }
    }
  }

  @include e(info) {
    float: left;
    width: 226px;
    height: 100%;
    padding: 22px 32px 0;
    background-color: #181818;
  }

  @include e(title) {
    font-size: 22px;
    color: #fff;
  }

  @include e(rule-link) {
    float: right;
    margin-top: 2px;
    font-size: 13px;
    color: #828282;
    text-decoration: underline;
    transition: all 0.2s;

    &:hover {
      color: $--color-primary;
    }
  }

  @include e(subtitle) {
    margin-top: 30px;
    font-size: 12px;
    line-height: 20px;
    color: #828282;

    & + & {
      margin-top: 0;
    }
  }

  @include e(link) {
    margin-top: 30px;
    font-size: 13px;
    color: $--color-primary;

    a:hover {
      text-decoration: underline;
    }
  }

  &:nth-child(2n+1) {
    margin-right: 20px;
  }
}
